body .product{
    align-content: center;
    align-items: center;
    border: none;
    bottom: 0;
    display: flex;
    flex-direction: column;
    height: 87%;
    overflow-x: hidden;
    overflow-y: auto;
    position: fixed;
    right: 0;
    width: 85%;
    z-index: 100;
}

.product select {
    appearance: none;
    background-color: transparent;
    border: none;
    cursor: inherit;
    font-family: inherit;
    font-size: .9rem;
    line-height: inherit;
    margin: 0;
    outline: none;
    padding: 0 1em 0 0;
    width: 100%;
    z-index: 1;
    -moz-appearance: none;
    -webkit-appearance: none;
}

.form-product-modal {
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
    width: 100%;
}

.form-product-modal select {
    appearance: none;
    background-color: transparent;
    border: none;
    cursor: inherit;
    font-family: inherit;
    font-size: .9rem;
    line-height: inherit;
    margin: 0;
    outline: none;
    padding: 0 1em 0 0;
    width: 100%;
    z-index: 1;
    -moz-appearance: none;
    -webkit-appearance: none;
}

.product select option { font-size: .90rem; }

.form-product-modal select option { font-size: .8rem; }

::placeholder {
    color: #999;
    opacity: .6; /* Firefox */
}

:-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: #999;
    opacity: .6;
}

::-ms-input-placeholder { /* Microsoft Edge */
    color: #999;
    opacity: .6;
}

.container-loading-product {
    align-items: center;
    display: flex;
    height: 100%;
    justify-content: center;
    width: 100%;
}

.container-loading-product .loading-product { color: #0191CE; }

/* Modal */
.ReactModalPortal > div{ opacity: 0; }

.ReactModalPortal .ReactModal__Overlay {
    align-items: center;
    display: flex;
    justify-content: center;
    overflow-y: scroll;
    transition: opacity .2s ease-in-out;
    z-index: 999;
}

.modal-fondo {
    background-color: rgba(0, 0, 0, 0.3);
    bottom: 0;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
}

.ReactModalPortal .ReactModal__Overlay--after-open { opacity: 1; }

.ReactModalPortal .ReactModal__Overlay--before-close { opacity: 0; }

.modal {
    background: #f7f7f7;
    border-radius: .7rem;
    color: rgb(51, 51, 51);
    display: inline;
    max-height: 620px;
    max-width: 500px;
    outline: none;
    padding: 10px 1.5rem;
}

.form-product-modal header {
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: .3rem;
}

.form-product-modal header p { margin: 0; }

.title-product-modal { margin: 0; }

.required-fields-modal-product {
    color: #0191CE;
    font-size: 10px;
    font-weight: 700;
}

hr{ 
    background-color: #ccc;
    border: none; 
    height: 1px;
    margin-bottom: 1rem;
}

.form-product-modal i {
    color: #0191CE;
    font-size: 15px;
    font-weight: 800;
}

.form-product-modal input {
    background: #FFFFFF;
    border: 1px solid #D2D2D2;
    border-radius: .2rem;
    box-sizing: border-box;
    height: 1.7rem;
    margin-top: .5rem;
    width: 17rem
}

.form-product-modal label, .form-product-modal input, .form-product-modal p {
    font-size: .9rem;
    margin: 0;
    margin-bottom: .5rem;
}

.form-product-modal input:focus {
    border: 1px solid rgb(3, 139, 198);
    box-shadow: 0 0 5px #15749d;
    outline: none;
}

.form-product-modal input:hover { border: 1px solid rgb(3, 139, 198); }

.codigo-product-input-modal {
    display: flex;
    flex-direction: row;
}

.codigo-product-input-modal p { padding-left: .5rem; }

.codigo-product-input-modal label, .codigo-product-input-modal p { font-size: 1rem !important; }

.precio1-product-input-modal, .nombre-product-input-modal, .select-container-product-modal {
    display: flex;
    flex-direction: column;
}

.precio1-product-input-modal input, .nombre-product-input-modal input {
    width: 100%;
}

.select-container-product-modal:nth-child(2n-1) { margin-top: .5rem; }

.select-container-product-modal label { margin-bottom: .5rem; }

.bodega-input-product-modal select, .grupo-input-product-modal select { 
    font-size: .75rem;
    height: 1.3rem; 
    width: 15rem;
}

.bodega-input-product-modal, .grupo-input-product-modal {
    align-items: center;
    background: #FFFFFF;
    border: 1px solid #D2D2D2;
    border-radius: .2rem;
    box-sizing: border-box;
    cursor: pointer;
    display: grid;
    grid-template-areas: "select";
    height: max-content;
    line-height: 1.1;
    padding: .2rem .5rem;
    position: relative;
    width: 100%;
}

.bodega-input-product-modal:focus, .grupo-input-product-modal:focus {
    border: 1px solid rgb(3, 139, 198);
    box-shadow: 0 0 5px #15749d;
    outline: none;
}

.bodega-input-product-modal:hover, .grupo-input-product-modal:hover { border: 1px solid #038bc6; }

.bodega-input-product-modal::after, .grupo-input-product-modal::after {
    background-color: #0191CE;
    clip-path: polygon(100% 0%, 0 0%, 50% 100%);
    content: "";
    grid-area: select;
    height: 0.5em;
    justify-self: end;
    position: absolute;
    width: 1em;
}

.checks-product-modal {
    display: flex;
    flex-direction: row;
    margin: .5rem 0; 
}

.iva-input-product-modal, .servicio-input-product-modal {
    display: flex;
    flex-direction: row;
}

.servicio-input-product-modal { margin-left: 2rem; }

.buttons-column {
    display: flex;
    justify-content: space-evenly;
}

.button-save-modal, .button-close-modal {
    background: none;
    border-radius: .4rem;
    font-size: 15px;
    font-weight: 600;
    letter-spacing: 0.05em;
    padding: .5rem 1rem;
}

.button-save-modal {
    border: 2px solid #0191CE;
    border-color: #0191CE;
    color: #0191CE;
}

.button-save-modal:hover {
    background: #0191CE;
    color: #FFFFFF;
    cursor: pointer;
}

.button-close-modal {
    border: 2px solid #ef020a;
    border-color: #ef020a;
    color: #ef020a;
}

.button-close-modal:hover {
    background: #ef020a;
    color: #FFFFFF;
    cursor: pointer;
}

.container-product-header { width: 95%; }

.container-product-header h1 {
    font-size: 20px;
    font-weight: 700;
    line-height: 24px;
    margin-bottom: .3rem;
    margin-top: 0;
}

.container-product-details input, .container-product-details label{ display: block; }

label {
    font-size: 15px;
    font-weight: 700;
    line-height: 18px;
}

.container-product-details {
    border: 1px solid #D2D2D2;
    border-radius: .5rem;
    padding: .5rem;
}

.container-product-details i {
    color: #0191CE;
    font-size: 15px;
    font-weight: 800;
}

.container-product-details input:focus {
    border: 1px solid rgb(3, 139, 198);
    box-shadow: 0 0 5px #15749d;
    outline: none;
}

.container-product-details input:hover { border: 1px solid rgb(3, 139, 198); }

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
    margin: 0; 
    -webkit-appearance: none; 
}

input[type=number] { -moz-appearance:textfield; }

.first-column-product-input, .second-column-product-input, .third-column-product-input {
    align-items: center;
    display: flex;
    justify-content: space-between;
    margin-bottom: .5rem;
    width: 100%;
}

.third-column-product-input { justify-content: flex-start; }

.first-column-product-input input, .second-column-product-input input, .third-column-product-input input {
    background: #FFFFFF;
    border: 1px solid #D2D2D2;
    border-radius: .2rem;
    box-sizing: border-box;
    height: 1.4rem;
    margin-top: .5rem;
}

.codigo-input-product input, .precio-input-product input { width: 10rem; }

.nombre-input-product input { width: 38rem; }

.select-container-product {
    display: flex;
    flex-direction: column; 
}

.select-container-product label { margin-bottom: .5rem; }

.bodega-input-product select, .grupo-input-product select { width: 22rem; }

.bodega-input-product, .grupo-input-product {
    align-items: center;
    background: #FFFFFF;
    border: 1px solid #D2D2D2;
    border-radius: .2rem;
    box-sizing: border-box;
    cursor: pointer;
    display: grid;
    grid-template-areas: "select";
    height: max-content;
    line-height: 1.1;
    padding: .2rem .5rem;
    position: relative;
}

.bodega-input-product:focus, .grupo-input-product:focus {
    border: 1px solid rgb(3, 139, 198);
    box-shadow: 0 0 5px #15749d;
    outline: none;
}

.bodega-input-product:hover, .grupo-input-product:hover { border: 1px solid #038bc6; }

.bodega-input-product::after, .grupo-input-product::after {
    background-color: #0191CE;
    clip-path: polygon(100% 0%, 0 0%, 50% 100%);
    content: "";
    grid-area: select;
    height: 0.5em;
    justify-self: end;
    position: absolute;
    width: 0.8em;
}

.iva-input-product, .servicio-input-product {
    display: flex;
    justify-content: center;
    margin-top: .5rem;
}

.servicio-input-product { margin-left: 2rem; }

input[type=checkbox] {
    appearance: none;
    cursor: pointer;
    height: 1rem;
    margin: 0;
    margin-left: .5rem;
    position: relative;
    width: 1rem;
    -moz-appearance: none;
    -webkit-appearance: none;
}

input[type=checkbox]:before {
    content: "";
    display: block;
    height: 1rem;
    left: 0;
    margin: 0;
    padding: 0;
    position: absolute;
    top: 0;
    width: 1rem;
}

input[type=checkbox]:checked:before {
    background-color:#0191CE;
    border: .1px solid #0191CE;
    border-radius: .1rem;
    content: "";
    display: block;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
}

input[type=checkbox]:checked:after {
    border: solid white;
    border-width: 0 2px 2px 0;
    content: "";
    display: block;
    height: .4rem;
    left: 6px;
    position: absolute;
    top: 2px;
    transform: rotate(45deg);
    width: .1rem;
    -ms-transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
}

.button-save-column-product {
    align-items: center;
    display: flex;
    justify-content: center;
    width: 100%;
}

.button-save-product {
    background: #0191CE;
    border: none;
    border-radius: 5rem;
    box-shadow: 3px 4px 4px rgba(1, 145, 206, 0.5);
    color: #FFFFFF;
    cursor: pointer;
    font-size: 1.2rem;
    font-weight: 700;
    height: 3rem;
    width: 15rem;
}

.button-save-product:hover {
    background: #15749d;
    box-shadow: 3px 4px 4px rgba(0, 64, 92, 0.5);
    color: #d8d8d8;
    cursor: pointer;
}

.required-fields {
    color: #0191CE;
    font-size: 12px;
    font-weight: 700;
    margin: 0;
    position: absolute;
    right: 1.3rem;
    top: 0;
}

.table-product {
    border: 1px solid #D2D2D2;
    border-radius: .5rem;
    margin-top: .5rem;
    overflow: hidden;
    width: 100%;
}

.table-product table {
    border-collapse: collapse;
    width: 100%;
}

.table-product table thead {
    background: rgba(1, 145, 206, 0.25);
    color: #0977A6;
    font-size: 1rem;
    font-weight: 700;
    text-align: center;
}

.table-product table thead th {
    padding: .5rem;
    text-align: center;
}

.table-product table tbody {
    background: #FFFFFF;
    font-size: .8rem;
    font-weight: 100;
}

.table-product table tbody tr:nth-child(even) { background-color: #f4f4f4; } 

.table-product table tbody tr td {
    padding: .5rem;
    text-align: center;
}

.table-product table tbody tr td:nth-child(n+3) { padding: .1rem .5rem; }

.table-product table tbody tr td .container-actions-product {
    align-items: center;
    cursor: pointer;
    display: flex;
    justify-content: center;
}

.table-product table tbody tr td .container-actions-1-product,
.table-product table tbody tr td .container-actions-2-product,
.table-product table tbody tr td .container-actions-3-product {
    align-items: center;
    border-radius: 5rem;
    display: flex;
    justify-content: center;
    padding: .3rem 1rem; 
}

.table-product table tbody tr td .container-actions-1-product { background: rgba(1, 145, 206, 0.85); }

.table-product table tbody tr td .container-actions-2-product { background: rgba(206, 26, 1, 0.85); }

.table-product table tbody tr td .container-actions-3-product {
    background: rgba(54, 206, 1, 0.85);
    padding: .2rem .8rem;
}

.pagination-product {
    display: flex;
    justify-content: center;
    margin-bottom: .5rem;
    margin-top: .5rem;
    width: 100%;
}

.pagination-1-product {
    align-items: center;
    background: #FFFFFF;
    border: 1px solid #D2D2D2;
    border-radius: 5rem;
    display: flex;
    height: 2rem;
    justify-content: space-between;
    overflow: hidden;
    width: 10rem;
}

.pagination-button-product {
    background: none;
    border-bottom: none;
    border-left: none;
    border-right: 1px solid #D2D2D2;
    border-top: none;
    cursor: pointer;
    font-size: 1.2rem;
    font-weight: 700;
    height: 2rem;
    width: 2rem;
}

.pagination-button-product:last-child {
    border-bottom: none;
    border-left: 1px solid #D2D2D2;
    border-right: none;
    border-top: none;
}

.pagination-1-product .pagination-num-product { display: flex; }

.pagination-1-product .current-page-product {
    color: #5a5a5a;
    font-size: 1.1rem;
    margin-right: .3rem;
}

.pagination-1-product .total-pages-product {
    font-size: 1.1rem;
    font-weight: 900;
}

.pagination-button-product:hover {
    background: #006a97;
    color: #FFFFFF;
}

@media only screen and (max-width: 1300px) {
    
    .nombre-input-product input { width: 30rem; }
    
}

@media only screen and (max-width: 1140px) {
    
    .codigo-input-product input, .precio-input-product input { width: 13rem; }
    
    .nombre-input-product input { width: 23rem; }
    
}

@media only screen and (max-width: 1040px) {
        
    .nombre-input-product input { width: 20rem; }
        
}

@media only screen and (max-width: 960px) {
    /* Ocultar SideBar */
    body .product {
        width: 100%;
        z-index: 0;
    }

}

@media only screen and (max-width: 810px) {
    
    .codigo-input-product input, .precio-input-product input { width: 10rem; }
    
    .nombre-input-product input { width: 20rem; }

    .bodega-input-product select, .grupo-input-product select { width: 20rem; }

}

@media only screen and (max-width: 750px) {
    
    .codigo-input-product input, .precio-input-product input { width: 9rem; }
    
    .nombre-input-product input { width: 17rem; }

    .bodega-input-product select, .grupo-input-product select { width: 17rem; }

}

@media only screen and (max-width: 650px) {
    
    .codigo-input-product input, .precio-input-product input { width: 7rem; }
    
    .nombre-input-product input { width: 14rem; }

    .bodega-input-product select, .grupo-input-product select { width: 13rem; }

    .table-product {
        border: none;
        margin-top: 1.5rem;
    }

    .table-product table thead { display: none; }

    .table-product table, .table-product table tbody, .table-product table tr, .table-product table td { display: block; }

    .table-product table tr { margin-bottom: .5rem; }

    .table-product table tbody tr td {
        border-bottom: 1px solid #D2D2D2;
        padding: .5rem;
        padding-right: 1rem;
        text-align: right;
    }

    .table-product table tbody tr td:nth-child(n+3) {
        padding: .5rem;
        padding-right: 1rem;
        text-align: right;
    }

    .table-product table td {
        position: relative;
        text-align: right;
    }

    .table-product table td::before {
        content: attr(data-label);
        font-size: 1rem;
        font-weight: 700;
        left: 0;
        padding-left: 1rem;
        position: absolute;
        text-align: left;
        width: 50%;
    }

    .table-product table tbody tr td .container-actions-product { justify-content: end; }

}

@media only screen and (max-width: 500px) {
    
    .codigo-input-product input, .precio-input-product input { width: 6rem; }
    
    .nombre-input-product input { width: 14rem; }

    .bodega-input-product select, .grupo-input-product select { width: 12rem; }

}

@media only screen and (max-width: 480px) {

    .first-column-product-input, .second-column-product-input { 
        align-items: flex-start;
        flex-direction: column; 
        margin: 0;
        padding-left: .5rem;
    }

    .third-column-product-input { 
        margin: 0;
        padding-left: .5rem;
    }

    .select-container-product {
        align-items: center;
        display: flex;
        flex-direction: row; 
        margin-bottom: 1rem;
        width: 100%;
    }
    
    .select-container-product label { margin: 0 !important; }

    .container-product-details input, .container-product-details label{
        display: inline-block;
        margin-bottom: 1rem;
    }

    .container-product-details label{
        text-align: end;
        width: 81.391px;
    }

    .container-product-details input { 
        border-radius: .5rem; 
        height: 2rem; 
    }

    .bodega-input-product, .grupo-input-product { 
        border-radius: .5rem; 
        height: 2rem; 
        margin-left: 1rem; 
        width: 70%;
    }

    .bodega-input-product select, .grupo-input-product select { width: 100%; }

    .codigo-input-product, .precio-input-product, .nombre-input-product { width: 100%; }

    .codigo-input-product input, 
    .precio-input-product input, 
    .nombre-input-product input { 
        margin-left: 1rem; 
        width: 70%; 
    }

    .iva-input-product, .servicio-input-product { margin: 0; }
    
    .iva-input-product input, .servicio-input-product input { 
        border-radius: .2rem; 
        height: 1rem; 
        margin-left: 1rem !important;
    }

    .servicio-input-product { margin: 0; }

}

@media only screen and (max-width: 450px) {
    
    .table-product table td:nth-child(2n) { font-size: .7rem; }

}

@media only screen and (max-width: 400px) {
    
    .table-product table td:nth-child(2n) { font-size: .6rem; }

}

@media only screen and (max-width: 380px) {
    .first-column-product-input, .second-column-product-input, .third-column-product-input { padding-left: 0rem; }
}

@media only screen and (max-width: 360px) {
    
    .container-product-details label{
        text-align: end;
        width: 75px;
    }

    .iva-input-product label, .servicio-input-product label { font-size: .8rem; }

}

@media only screen and (max-width: 350px) {
    
    .container-product-header { min-width: 332.5px; }

}

@media only screen and (max-height: 500px) {

    body .product { height: 80%; }

    .ReactModalPortal .ReactModal__Overlay {
        align-items: flex-start;
        justify-content: center;
    }

    .modal { margin: 1rem 0; }

}